.profile__avatar-button {
  position: absolute;
  border-radius: 40%;
  width: 120px;
  height: 120px;
  align-items: center;
  border: none;
  outline: none;
  padding: 0;
  cursor: pointer;
  background: transparent;
}

.profile__avatar-button:hover {
  background-color: rgba(0, 0, 0, 0.8);
  background-image: url(../../../images/ava-button.svg);
  background-position: center;
  background-repeat: no-repeat;
}
