.card__trash-button {
  background: url(../../../images/trash-button.svg);
  position: absolute;
  width: 18px;
  height: 19.3px;
  top: 18px;
  right: 15px;
  border: none;
  outline: none;
  cursor: pointer;
}

.card__trash-button:hover {
  opacity: 0.6;
  transition: opacity .3s ease;
}
